"use client"
import React, { useState } from 'react'
import style from "./style.module.css"
import '../globals.css'

function page() {

  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((cur) => cur + 1)
  }

  const steps = [
    {
      step: 1,
      about: "Course Information",
      // ele: <FirstStep handleNext={handleNext} />
    },
    {
      step: 2,
      about: "Course Option",
      // ele: <SecoundStep handleNext={handleNext} />
    },
    {
      step: 3,
      about: "Course Content",
      // ele: <FirstStep handleNext={handleNext} />
    },
    {
      step: 4,
      about: "Course Preview",
      // ele: <FirstStep handleNext={handleNext} />
    }
  ];

  return (
    <main>
      <div className={`${style.bgLeftSide}  p-5  h-screen w-[500px]`}>
        <span className='text-white font-medium mt-20 block'>Tell us about your company</span>
      </div>
      <div className=' p-5 w-[75%] bg-white h-[800px] shadow-2xl rounded-xl absolute top-1/2 bottom-1/2 left-1/2 right-1/2 -translate-x-1/2 -translate-y-1/2 '>
        <h1 className='text-center'>Creat a shipping company account</h1>
        <div>
          <div>
            <Stepper
              activeStep={activeStep}
              isLastStep={(value) => setIsLastStep(value)}
              isFirstStep={(value) => setIsFirstStep(value)}
            >
              <Step onClick={() => setActiveStep(0)}>1</Step>
              <Step onClick={() => setActiveStep(1)}>2</Step>
              <Step onClick={() => setActiveStep(2)}>3</Step>
            </Stepper>
          </div>
        </div>
      </div>
    </main>
  )
}

export default page
